<template>
  <NavAndAll/>
  <div style="width: 100%; height: 100vh;" ref="changepage">
  <div id="hr"></div>
    <router-view/>
</div>
</template>

<script>
import NavAndAll from './components/NavAndAll.vue'
import Hammer from 'hammerjs';

export default {
  name: 'App',
  components: {
   NavAndAll,
  },
  mounted() {
    const changepage = this.$refs.changepage;
    const hammer = new Hammer(changepage);

    hammer.on('swipeleft', () => {
      switch (this.$route.name) {
        case 'Home':
          window.location.href = '../blog';
          break;
        case 'blog':
          window.location.href = '../App';
          break;
        case 'App':
          window.location.href = '../Game';
          break;
        case 'Game':
          window.location.href = '../Other';
          break;
        case 'Other':
          window.location.href = '../Pay';
          break;
      }
    });
    hammer.on('swiperight', () => {
      switch (this.$route.name) {
        case 'Pay':
          window.location.href = '../Other';
          break;
        case 'Other':
          window.location.href = '../Game';
          break;
        case 'Game':
          window.location.href = '../App';
          break;
        case 'App':
          window.location.href = '../blog';
          break;
        case 'blog':
          window.location.href = '../';
          break;
      }
    });
  }
}

</script>

<style>
  body{
    background-color: #111 !important;
    overflow: hidden;
}
  #hr{
    position: fixed;
    width: 110vw;
    top:0%;
    left: -5vw;
    height: 1px;
    background-color: rgb(0, 104, 71);
    animation-name:hrMove,hrColor;
    animation-duration:4s, 1.5s;
    animation-timing-function: linear,linear;
    animation-iteration-count: infinite,infinite; 
    z-index: -1;
  }

  @keyframes hrMove {
  from {top:0%;}
  to {top:100%;}}

  @keyframes hrColor {
  from {background-color: rgb(0, 104, 71);}
  to {background-color: #111;}}
</style>
